export const createPage10Code = ({

}) => {
  const article = document.createElement('article');

  const section = `
  <script src="/Style/prism.js"></script>
  <link rel="stylesheet" href="/Style/prism.css">
  <link rel="stylesheet" href="/Style/SummaryStyle.css">
  <p>In diesem Dokument, wird der Source-Code der Aufgaben 10 dargestellt. Hierbei wurde die Menü Komponente und der WWW-Navigator als Single File Component (.vue Datei) geschrieben und automatisch umgewandelt</p>
  

  <details>
  <summary> Source-Code WordCounter.js</summary>


  <pre>
		<code class="language-javascript">
		Vue.component('word-counter', {
			data() {
				return {
					value: "",
					countSymbols: 0,
					countLetters: 0,
					countSpace: 0,
					countWords: 0
				}
			},
			methods: {
				CalcCount: function(){
		
					this.countSymbols =""+ this.value.length;
					this.countLetters = this.countFunc(this.value.split(''), (v) => { return v!==' ' && v!=='\n'});
					this.countSpace = this.countFunc(this.value.split(''), (v) => { return v===' '});
					this.countWords = this.countFunc(this.value.split(' '), (v) => { return v.length>0});
				},
				countFunc: function(val,f)
				{
					var result = 0;
					for(var i = 0; i < val.length;i++)
					{
						if(f(val[i]))
							result++;
					}
					return result;
				}
			},
			template: \`	<div>
							<textarea  v-model="value" @input="CalcCount" style="width:100%; height:200px;" > </textarea > 
							<p>Länge Zeichenkette: {{countSymbols}}</p>
							<p>Anzahl Buchstaben: {{countLetters}}</p>
							<p>Anzahl Leerzeichen: {{countSpace}}</p>
							<p>Anzahl Woerter: {{countWords}}</p> 
						</div>\`
		})
		
		</code>
	</pre>
	</details>
	<a href="/U7/Alpha.txt">Beispieldatei mit 10000 Zeilen (Buchstaben)</a>
	<br>
	<br>
	<details>
	<summary> Source Code des Zahlen Genereator (number_file_gen.mjs)</summary>
  
  
	<pre>
		  <code class="language-javascript">
		import http from 'http';
		import fs from "fs"




		export function genNumberFile(amount,filename,callback)
		{
			console.log("Create: " + amount +" lines");
			if(filename === undefined)
				filename = "output.txt";
			
			
			
			return new Promise((resolve, reject) => {
				fs.writeFile(filename, createString(amount), function(err) {
				if (err) {
					reject();
					return console.error(err);
				}
				console.log("Es wurde eine Datei '" + filename + "' mit " + amount+ " Zeilen erstellt.");
				if(callback !== undefined)
					callback();
				resolve();
			});
			});
		}
		


		  
		  </code>
	  </pre>
	  </details>
	  
	<a href="/U7/number.txt">Beispieldatei mit 10000 Zeilen (Zahlen)</a>




`;

  article.insertAdjacentHTML('beforeend', section);

  return section;
};
